.progress-bar-container {
  position: absolute;
  top: 100px;
  left: 50%; /* 左右中央の基準位置 */
  transform: translateX(-50%); /* 中央に揃える */
  width: 90%;
  background-color: #d7f7fd;
  border-radius: 8px;
  overflow: hidden;
  height: 20px;
  margin: 10px auto;
}

.progress-bar {
  height: 100%;
  background-color: #1E899D;
  width: 0%; /* 初期値: 0% */
  transition: width 0.3s ease-in-out;
}
