/* 共通変数の定義 */
:root {
  --bg-color-main: #f4f4f9;
  --hover-bg-color: #e8f0fe;
  --text-color-main: #333;
  --border-color-main: #ccc;
}

/* チャット画面の全体スタイル */
.chat-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  background-color: var(--bg-color-main);
  padding: 10px;
  position: relative;
}

/* チャットボックス */
.chat-box {
  flex-grow: 1;
  overflow-y: auto;
  padding: 10px;
  margin-bottom: 60px; /* メッセージ入力エリアが隠れないように */
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: flex-start;
}

/* メッセージの親要素 */
.message {
  display: flex;
  align-items: flex-start;
  margin: 10px 0;
}

.message.user {
  align-items: flex-end;
  justify-content: flex-end; /* 親要素を右寄せ */
}

.message.bot {
  justify-content: flex-start;
}

.message-bubble {
  max-width: 75%;
  padding: 12px;
  border-radius: 18px;
  font-size: 16px;
  line-height: 1.4;
  word-wrap: break-word;
  margin-bottom: 10px;
  display: inline-block;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
}

/* ボットメッセージのスタイル */
.message.bot .message-bubble {
  background-color: #e0e0e0;
  color: var(text-color-main);
  border-radius: 18px 18px 18px 0;
}

/* ユーザーメッセージのスタイル */
.message.user .message-bubble {
  background-color: #1E899D;
  color: white;
  border-radius: 18px 18px 0 18px;
}

/* メッセージ入力エリア */
.input-container {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 99%; /*送信ボタンの右端が見切れないように100ではなく99 */
  background-color: white;
  padding: 10px;
  border-top: 1px solid #ddd;
}

.input-container input {
  flex-grow: 1;
  padding: 10px;
  font-size: 16px;
  border-radius: 25px;
  border: 1px solid #ddd;
  margin-right: 10px;
}

.input-container button {
  padding: 10px 20px;
  background-color: #1E899D;
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
}

.input-container button:hover {
  background-color: #14667F;
}

/* ホームボタンのスタイル */
.home-button {
  position: absolute;
  top: 30px;
  right: 70px;
  font-size: 1rem;
  color: var(text-color-main);
  border: 1px solid var(--bg-color-main);
  border-radius: 6px;
  background-color: var(--bg-color-main);
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.home-button:hover {
  background-color: var(hover-bg-color);
}

.home-icon {
  width: 20px; /* ロゴ画像の幅（調整可能） */
  height: auto;
}

/* 共通アイコンスタイル */
.bot-icon,
.user-icon {
  width: 50px;
  height: auto;
  background-color: #d1e8ff;
}

/* メッセージアイコン */
.message-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.message.bot .message-icon {
  margin-right: 10px;
  order: 0;
}

.message.user .message-icon {
  margin-left: 10px;
  order: 2;
}

.survey-link {
  position: fixed;
  bottom: 10px;
  left: 50%; /* 横方向の中央に配置 */
  transform: translateX(-50%); /* 中央揃え調整 */  
  background-color: #d1e8ff;
  padding: 8px 12px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  font-size: 16px;
  color: var(text-color-main);
  z-index: 1000; /* 他の要素の上に表示 */
}

.survey-link a {
  color: #007bff;
  text-decoration: underline;
}

.survey-link a:hover {
  text-decoration: none;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #d1e8ff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  font-size: 16px;
  color: var(text-color-main);
  text-align: center;
  z-index: 1000;
}

.popup a {
  color: #007bff;
  text-decoration: underline;
}

.popup a:hover {
  text-decoration: none;
}