/* 全体のレイアウト */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* 水平中央揃え */
  justify-content: center; /* 垂直中央揃え */
  height: 100vh; /* 画面の高さ全体を使用 */
  text-align: center; /* テキストを中央揃え */
  background-color: #f0f0f0; /* 背景色の変更（必要に応じて） */
}
  
/* ロゴのスタイル */
.logo-container {
  margin-bottom: 5px; /* ロゴとテキストの間にスペースを追加 */
}
  
.logo {
  width: 400px; /* ロゴ画像の幅（調整可能） */
  height: auto;
}
  
/* タイトルのスタイル */
.title {
  font-size: 2px; /* フォントサイズ */
  margin-bottom: 20px; /* タイトルとボタンの間にスペースを追加 */
}

.input {
  width: 40%; /* 横幅を画面の40%に設定 */
  padding: 10px;
  margin: 15px 0; /* 入力欄の上下間隔を調整 */
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  box-sizing: border-box; /* パディングを含む幅計算 */
}

.error-message {
  color: red;
  font-size: 14px;
  margin: 15px 0; /* 入力欄の上下間隔を調整 */
}

/* ボタンのスタイル */
.consult-button {
  padding: 20px 40px; /* ボタンの大きさを2倍に調整 */
  margin-top: 30px; /* ボタンと入力欄の間隔を広く */
  font-size: 20px; /* フォントサイズ（必要に応じて調整） */
  background-color: #1E899D; /* ボタンの背景色 */
  color: white; /* ボタンのテキスト色 */
  border: none;
  border-radius: 5px; /* 角丸 */
  cursor: pointer;
  transition: background-color 0.3s ease;
}
  
/* ボタンのホバー時の効果 */
.consult-button:hover {
  background-color: #14667F; /* ホバー時の背景色 */
}
  