/* 共通変数の定義 */
.root {
  --bg-color-main: #f4f4f9;
  --hover-bg-color: #e8f0fe;
  --text-color-main: #333;
  --border-color-main: #ccc;
}

/* 全体のコンテナ */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #1E899D;
}
  
/* カード（フォーム）のスタイル */
.card {
  padding: 60px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
}
  
/* 入力フィールドのスタイル */
.input {
  margin: 10px 0;
  padding: 10px;
  width: 95%;
  border-radius: 10px;
  border: 1px solid var(border-color-main);
}
  
/* ボタンのスタイル */
.login-button {
  padding: 10px;
  width: 100%;
  font-size: 15px; /* フォントサイズ（必要に応じて調整） */
  color: white;
  background-color: #C45C49;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
  
.login-button:hover {
  background-color: rgba(199, 83, 63, 0.781);
}

button:disabled {
  background-color: gray;
  cursor: not-allowed;
  opacity: 0.5;
}

.logo-text {
  display: flex;
  font-size: 50px;
  font-weight: bold;
  color: #1E899D;
  margin: 10px 0; /* 外側余白 */
  text-align: center;
  justify-content: center; /* 水平方向に中央揃え */
}

.title-text {
  font-size: 20px;
  font-weight: bold;
  color: var(--text-color-main);
  margin: 20px 0;
  text-align: center;
}    

.ic {
  vertical-align: middle; /* テキストと水平に配置 */
  margin-right: 0.1em; /* 文字との余白を調整 */
  width: 70px; /* ロゴ画像の幅（調整可能） */
  height: auto;
}
  